import { List } from 'antd';

export type TUser = {
  did: string;
  name: string;
  certificationName: string;
  certificatedAte: string;
  expiredAt: string;
  issuerName: string;
  transactionHash: string;
  imageChecksum: string;
};

type TProps = {
  user: TUser;
}

export default function Profile({ user }: TProps) {
  const transformMap: string[] = ['Did', 'Name', 'Certification Name', 'Certificated At', 'Expired At', 'Issuer Name', 'Transaction Hash', 'Image Checksum'];
  return (
    <List
    itemLayout="horizontal"
    dataSource={transformMap}
    renderItem={(key: string) => (
      <List.Item>
        <div>
        <span className="name">{key}: </span><span>{user.name}</span>
      </div>
      </List.Item>
    )}
  />
  );
}
